<template>
  <div id="ResourceAnnounce">
    <span class=resannouline></span>
    <h3 class='resannoutitle'></h3>
    <ul class='resannouitems'>
      <li v-for="(resannouitem, index) in resAnnounceItems"
          v-bind:key='index'
          class='resannouitem'>
        <div class='resannouitemtitle'>{{resannouitem.title}}</div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'ResourceAnnounce',
  data () {
    return {
      resAnnounceItems: [
        {
          title: '',
          url: ''
        },
        {
          title: '',
          url: ''
        },
        {
          title: '',
          url: ''
        }
      ]
    }
  }
}
</script>

<style>
#ResourceAnnounce{
  margin-left: 20px;
}
.resannouline{
  padding-left: 10px;
  margin-left: 30px;
  background: #2E86C1;
}
.resannoutitle{
  color: #2E86C1;
  padding-left: 20px;
  margin-bottom: 10px;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  display: inline-block;
}
.resannouitems{
  list-style-type:none;
  margin-left: 10px;
  margin-right: 10px;
  margin-top: 0;
  padding-left: 20px;
  padding-right: 20px;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
}
.resannouitem{
  margin: 0;
  height: 30px;
  width: 100%;
}
.resannouitem:hover{
  background: #F2F3F4;
  cursor: pointer;
}
.resannouitemtitle{
  color: #626567;
  margin-top:5px;
  margin-left: 5px;
  vertical-align: middle;
}
</style>



